body {
  background-color: #22254b;
}
.app-container {
  /*movie.js컴퍼넌트를 담고 있는 App.js의 app-container를 이용하여 한 줄에 적당하게 포스터를 배치 */
  display: flex; /*전체 이미지의 폭이 브라우저의 폭보다 크다면 이미지의 폭을 조정하여 한줄에 배치하는 것이 기본*/
  flex-wrap: wrap; /*nowrap이 기본값이라 안써도 꽉차게 이미지가 한줄에 배치되므로 wrap을 쓰면 지정된 폭보다 브라우저폭이 작으면 알아서 개행이 된다. */
  justify-content: center; /*화면의 중앙 정렬*/
}
.movie-container {
  width: 250px;
  margin: 16px;
  background-color: #373b69;
  color: #ffffff;
  border-radius: 5px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
}

.movie-container img {
  max-width: 100%;
}

.movie-info {
  /*movie.js컴퍼넌트 */
  display: flex; /* 두 줄 같은 한줄로 처리가 된다.*/
  padding: 20px; /* 안쪽 여백을 주어 양쪽에 딱 붙지 않게 함*/
  justify-content: space-between; /* 평점이 왼쪽으로 오는데 오른쪽에 붙이기 위해서 */
  align-items: center; /*마치 두 줄처럼 보이므로 한 줄로 보이게 하기 위해 */
}

.movie-info h4 {
  margin: 0; /* h4가 가지고 있는 기본 여백을 없앰. */
}

.movie-info span {
  margin-left: 20px; /* 제목과 평점이 너무 붙어있으면 사용하고 그렇지 않다면 굳이 사용하지 않아도 됨. */
}
